
import { Injectable } from '@angular/core';
import * as amplitude from 'amplitude-js';
import { EventProperty } from '../models/event-property';
@Injectable()
export class DataAnalyticsService {
  private API_KEY="48cfab7cd407a06295763cf5e4821d52";
  constructor(   ) {
      this.inItData();
  }


private inItData(){
  amplitude.getInstance().init(this.API_KEY);


}
logEvent(eventName:string,eventProperty:EventProperty){
  amplitude.getInstance().logEvent(eventName,eventProperty);
}

}
