import { Component, OnInit } from '@angular/core';
import { EventProperty } from '../models/event-property';
import { DataAnalyticsService } from '../services/data-analytic.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  constructor(private dataService: DataAnalyticsService) { }

  ngOnInit(): void {
    let event : EventProperty = {
      pageName:'Home'
    };
    this.dataService.logEvent("Visit",event);
  }

}
